.course-card-bottom {
  z-index: 2;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.75));
}
.course-card-bottom .product-title {
  color: #fff;
  text-shadow: 0 0 60px black;
}

.course-completion {
  position: absolute;
  width: 100%;
  height: 4px;
  overflow: hidden;
}

.course-completion .percentage {
  position: absolute;
  top: 0;
  left: -100%;
  transform: translateX(var(--course-completion, 0%));
  width: 100%;
  height: 100%;
  background: gold;
}
